header {
  color: white;
  height: 100px;
  padding: 0 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}
.logo {
  font-family: 'Gloria Hallelujah', cursive;
  font-size: 24px;
}

.logo span {
  color: yellow;
}
.shader {
  position: absolute;
  background: rgba(0, 0, 0, 0.4);
  left: 0;
  right: 0;
  height: 100%;
}

header img {
  height: 100%;
}

.video-container {
  position: relative;
  -webkit-box-shadow: 0px 7px 22px rgba(0,0,0,0.3);
          box-shadow: 0px 7px 22px rgba(0,0,0,0.3);
}

video {
  width: 100%;
}

nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  font-weight: bold;
}
nav > div {
  padding: 0 10px;
}

.cards {
  width: 70%;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding: 50px 0;
  color: black;
}

footer {
  height: 270px;
  background: #f2f2f2;
  -webkit-box-shadow: 0px 0px 22px rgba(0,0,0,0.3);
          box-shadow: 0px 0px 22px rgba(0,0,0,0.3);
}



